.container-puzzle {
  display: grid;
  width: 750px;
  height: 600px;
  grid-auto-flow: dense;
  grid-template-columns: repeat(25, 30px);
  grid-template-rows: repeat(20, 30px);
  background-color: #e6e5e5;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

